.header{
    margin-top: 60px;
}
.headerTitles{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Lora', serif;
    color: #444;    
}
.headerTitleSm{
    position: absolute;
    top: 25%;
    font-size: 20px;
}
.headerTitleLg{
    position: absolute;
    top: 35%;
    font-size: 100px;
    
}
.headerImg{
    width: 100%;
    height: 300px;
    margin-top: 0px;
    object-fit: cover;
}